.form {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 10px;
}

.input {
  border: 1px solid black;
  border-radius: 5px;
  -webkit-flex-basis: 80%;
          flex-basis: 80%;
  background-color: transparent;
  outline: none;
  padding: 10px;
  padding-left: 15px;
}

.form-button {
  background-color: rgb(38, 0, 255);
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  outline: none;
  color: white;
  cursor: pointer;
  -webkit-flex-basis: 15%;
          flex-basis: 15%;
}

.form-button:hover {
  opacity: 0.9;
}

.form-button:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.loader-wrapper {
  min-height: 100px;
  margin: 0 auto;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.loader-wrapper .loader-circle {
  display: block;
  width: 20px;
  height: 20px;
  margin: 10px;
  border-radius: 50%;
  background-color: blue;
  -webkit-animation: 1s circleMovement infinite;
          animation: 1s circleMovement infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.loader-wrapper .loader-circle:nth-child(2) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loader-wrapper .loader-circle:nth-child(3) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.loader-wrapper .loader-circle:nth-child(4) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}

@-webkit-keyframes circleMovement {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  75% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes circleMovement {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  75% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.todoItem {
  padding: 10px 20px;
  font-size: 18px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid rgb(212, 202, 202);
  position: relative;
}

.todoItem label {
  -webkit-flex-basis: 100px;
          flex-basis: 100px;
  text-align: left;
}

.todoItem input[type='checkbox'] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid blue;
  border-radius: 4px;
  outline: none;
  vertical-align: middle;
  margin-right: 10px;
}

.todoItem span {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
  text-align: left;
}

.todoItem input[type='checkbox']:checked {
  background-color: blue;
}

.todoItem input[type='checkbox']:focus,
.todoItem input[type='checkbox']:hover {
  box-shadow: 0 0 0 3px rgb(121, 109, 231);
  cursor: pointer;
}

.todoItem button {
  background-color: blue;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 10px 15px;
}

.list {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  list-style-type: none;
  padding: 0;
}

.App {
  text-align: center;
  padding: 20px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
